








































































import { Component, Vue } from 'vue-property-decorator';
import Header from '@/components/Header.vue';

@Component({
  components: {
    Header,
  },

})

export default class dailyCheck extends Vue {
    private date:any
    private text:Number=1
    private showEnregistre:boolean=false
    private loading?: boolean = false;
        private finished?: boolean = false;
        private refreshing?: boolean = false;
        private enregistrements: any[] = [];;
        private loadData(): void {
    setTimeout(() => {
      if (this.refreshing) {
        this.enregistrements = [];
        this.refreshing = false;
      }
      this.enregistrements = [
        {
          date:'2020.08.14',
          time:'23:00:09',
          day:'004天',
          points:'1分'
      },
      {
          date:'2020.08.14',
          time:'23:00:09',
          day:'003天',
          points:'1分'
      },
      {
          date:'2020.08.14',
          time:'23:00:09',
          day:'002天',
          points:'1分'
      },
      {
          date:'2020.08.14',
          time:'23:00:09',
          day:'001天',
          points:'1分'
      },
      
    ]
    console.log(this.enregistrements)
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
    formatDate(date:Date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    }
    showEnregistrement(){
        this.loadData()
        this.showEnregistre=true
    }
    onConfirm(date:any) {
      const [start, end] = date;
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
        
    var Difference_In_Time = end.getTime() - start.getTime(); 
  
// To calculate the no. of days between two dates 
    var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24);
      this.text = Difference_In_Days
    }
}
